aside{
	nav{
		position: relative;
		padding: 30px 10px;
		background-color: $gray;

	}

}
.sidebar-menu{
	position: relative;
	z-index: 99;
}
.mobile-menu{
	display: none;
	padding-top: 30px;
	background-color: $gray;
}
.sidebar-menu__item,
.submenu-item{
	position: relative;
	list-style-type: none;
	border-bottom: 1px solid $border;
	&:last-child{
		border-bottom-color: transparent;
	}
}
.sidebar-menu__link, .submenu-link{
	display: block;
	padding: 12px 12px 12px 50px;
	color: $accent;
	text-transform: uppercase;
	&:hover, &:active{
		font-family: $roboto-b;
		color: $accent;
		text-decoration: none;
	}
	&:last-child{
		border-bottom: none;
	}
	
}

.submenu-link{
	display: block;
	padding: 10px 0;
	text-transform: none;
	font-size: 14px;

}
.submenu{

}
.sf-menu{
	float: none;
}
.sf-vertical{
	width: auto;
}
.sf-menu ul{
	right: 100%;
	left: auto;
	width: 240px;
	margin-right: 10px;
	background-color: $gray;
	box-shadow: none;
}
.sf-menu li{
	white-space: normal;
	background-color: transparent;
}
.sf-menu li:hover, .sf-menu li.sfHover{
	background-color: transparent;
}
.sf-menu ul li{
	border-bottom: 1px solid $border;
	background-color: transparent;
	&:last-child{
		border-bottom: none;
	}
}
.sf-menu a{
	padding: 12px 12px 12px 50px;
	color: $accent;
	text-transform: uppercase;
	border: none;

	&:hover, &:active{
		font-family: $roboto-b;
		color: $accent;
		text-decoration: none;
	}
}
.sf-menu .submenu{

	padding: 14px 10px;
	border-right: 1px solid $border;
}
.sf-menu .submenu li a{
	display: block;
	padding: 10px 12px;
	text-transform: none;
	font-size: 14px;
}
.sf-arrows .sf-with-ul{
	padding-right: 0;
}
.sf-arrows .sf-with-ul::after{
	display: block;
background-image: url(../img/sprite.png);
background-position: -43px -53px;
width: 6px;
height: 9px;
	border: none;
	left: 25px;

}
.chevron-left{
	display: block;
	position: absolute;
	left: 15px;
	top: 8px;
	width: 30px;
	height: 30px;
	
	background:  url(../img/icons/chevron-left.png) center no-repeat;
	// @include sprite($chevron-left);
	cursor: pointer;
}
.news{

	margin-top: 30px;
}
.big-title{
	padding: 20px 0;
	margin: 0;
	margin-bottom: 20px;
	background-color: $gray;
	color: $accent;
	font-size: 24px;
	text-transform: uppercase;
	text-align: center;
}
.article-prewiew{
	margin-bottom: 10px;

}
.article-prewiew__img{
	width: 98px;
	height: 94px;
	float: left;
	// padding: 4px;
	position: relative;
	border: 1px solid $accent;
	// overflow: hidden;
	
	&--big{
		width: 100%;
		max-width: 360px;
		height: 226px;
		float: none;
		margin-bottom: 14px;

		.article-prewiew__img-inner{
			width: 94%;
			height: 92%;
			right: 0;
			left: 0;
			img{
				max-width: 100%;
				max-height: none;
			}
		}
	}
}
.article-prewiew__img-inner{
	
	@include absolute-center;
	right: 4px;
	left: auto;
	width: 90%;
	height: 90%;
	overflow: hidden;
	text-align: center;

	img{
		@include absolute-center;
		// display: inline-block;
		// margin: 4px;
		max-height: 100%;
		left: 50%;
		transform: translateX(-50%);
		// max-width: 100%;
	}
}
.article-prewiew__text{
	margin-left: 107px;

	p{
		margin: 0;
		height: 54px;
		font-size: 14px;
		line-height: 18px;
		color: $text-gray;
		overflow: hidden;
		font-style: italic;
	}
}
.main-article{
	margin-bottom: 40px;
}
.mobile-menu{
	.submenu{
		display: none;
		padding-left: 30px;
	}
}