.category{
	h3{
		&:before{
			display: none;
		}
	}
	p{
		height: 168px;
		overflow: hidden;
	}


}
.category-item{
	@include clearfix
	margin-bottom: 20px;

}
.pager-wrapper{
	text-align: center;
}
.content-pager{
	display: inline-block;
	margin: 0 auto;
	margin-bottom: 85px;
}
.pager-prev,
.pager-body,
.pager-next{
	display: inline-block;
	vertical-align: middle;
}
.pager-prev,
.pager-next,
.pager-body li{
	width: 29px;
	height: 29px;
	border-radius: 50%;
	border: 1px solid $accent;
	background: center no-repeat;
	text-align: center;
}
.pager-prev,
.pager-next{
	position: relative;
	cursor: pointer;
	&:hover, &:active{
		background-color: $accent;
	}
	
}
.pager-prev{
		&:after{
	
	content: '';
	display: block;
	@include sprite($pager-left);
	@include absolute-center;
	}
	// background-image: url(../img/icons/pager-left.png);
	&:hover, &:active{
		&:after{
			@include sprite($pager-left-active);
		// background-image: url(../img/icons/pager-left-active.png);
			
		}
	}
	
}
.pager-next{
	&:after{
		content: '';
		display: block;
		@include sprite($pager-right);
		@include absolute-center;
	}
	// background-image: url(../img/icons/pager-right.png);
	&:hover, &:active{
		&:after{
			@include sprite($pager-right-active);
		// background-image: url(../img/icons/pager-right-active.png);

		}
	}
}
.pager-body{
	margin: 0 14px;
	font-size: 0;
	li{
		border: none;
		display: inline-block;
		list-style-type: none;
		a{	
			display: inline-block;
			padding: 0;
			font-family: $roboto-b;
			text-decoration: none;
			color: #000;
			font-size: 18px;
			text-align: center;
			line-height: 26px;
			border: none;
		}
		&:hover, &:active{
			background-color: $accent;
			a{
				color: #fff;
			}
		}
		
		
	}
}