/*==========  Desktop First Method  ==========*/

 

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px){
	.news{
		.article-prewiew__text{
			p{
				height: 36px;
			}
		}
	}
	.main-article{
		.article-prewiew{
			
			.article-prewiew__img{
				height: 73px;
				.article-prewiew__img-inner{
					// height: 87px;
				}
				img{
					max-height: none;

				}


			}

			p{
				display: none;
			}
		}
	}
	.article-prewiew__img--big{
		height: 185px;
	}
	.sf-menu {
		a{

			padding-left: 23px;
		}
		.chevron-left{
			left: -7px;
		}
	}
	.category{
		p{
			height: 126px;
		}
	}

}
	
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px){
	.main-article{
		.article-prewiew{

			.article-prewiew__img{
				height: 94px;
				.article-prewiew__img-inner{
					height: 90%;
				}
				img{
					max-height: none;

				}

			}

			p{
				display: block;
				height: 36px;
			}
		}
	}
	.article-prewiew__img--big{
		height: 218px;
		.article-prewiew__img-inner{
			height: 90%;
		}
	}
	.sf-menu{
		display: none;
	}
	.news{
		display: none;
	}
	aside nav{
		padding: 0;
		min-height: 30px;
		background-color: transparent;
	}
	.sf-menu{
		position: absolute;
		right: 0;
		width: 50%;
		padding-top: 30px;
		background-color: $gray;
	}
	.sf-menu ul{
		margin-right: 0;
	}
	.sf-menu {
		a{

			padding-left: 50px;
		}
		.chevron-left{
			left: 14px;
		}
	}
	.header-logo{
		width: 97px;
		height: 97px;

		img{
			width: 60%;
		}

		span{
			margin-top: 66px;
			font-size: 10px;
		}
	}
	.header-top{
		.menu__link{
			font-size: 14px;
		}
		.menu__item{
			margin-left: 17px;
			&:first-child{
				margin-left: 0;
			}
		}
	}

	.banner{
		display: none;
	}

}

	
	
/* Small Devices, Tablets */
@media only screen and (max-width : 768px){

	.article-prewiew__img--big{
		height: auto;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 15px;
		.article-prewiew__img-inner{
			position: static;
			padding: 10px;
			width: auto;
			height: auto;
			overflow: visible;
			img{
				position: static;
				display: block;
				width: auto;
				height: auto;
				max-width: 100%;
				transform: none;
			}
		}
	}
	.toggle-mnu--desktop{
		display: none;
	}
	.mobile-menu{
		margin-top: -10px;
	}
	.sf-menu{
		display: none !important;
	}
	.header-top{
		height: 76px;
		padding: 0;

		nav{
			position: relative;
			width: 100%;
			float: none;
			text-align: center;
			z-index: 1;
		}
		.menu{
			display: inline-block;
		}
	}
	.header-logo{
		top: 58px;
		z-index: 0;
	}
	.header-search{
		top: 0;
		margin-top: 10px;
	}
	footer{
		font-size: 16px;

		nav, .footer-copyright{
			float: none;
			width: 100%;
			text-align: center;
		}
		nav{
			margin-bottom: 15px;
		}
		.menu__item{
			display: block;
			margin-left: 0;
			// text-align: center;
		}
	}

	article{
		img{
			float: none;
			display: block;
			margin-left: auto;
			margin-right: auto;
		}

		figure{
			padding-right: 15px;
			div{
				float: none;
				width: auto;
				height: inherit;
				max-width: 100%;
				display: block;
				margin-left: auto;
				margin-right: auto;
				text-align: center;

				img{
					max-width: 100%;
					display: inline-block;
				}
			}

			p{
				margin-left: 0;
			}

		}

		blockquote{
			padding-left: 5px;
			padding-top: 124px;
			background-position: center 45px;
		}
		iframe{
			height: 315px;
		}
	}

	/*Disable Animation on Mobile Devices*/
	.animated{
		
		/*CSS transitions*/
		-o-transition-property: none !important;
		-moz-transition-property: none !important;
		-ms-transition-property: none !important;
		-webkit-transition-property: none !important;
		transition-property: none !important;
		/*CSS transforms*/
		-o-transform: none !important;
		-moz-transform: none !important;
		-ms-transform: none !important;
		-webkit-transform: none !important;
		transform: none !important;
		/*CSS animations*/
		-webkit-animation: none !important;
		-moz-animation: none !important;
		-o-animation: none !important;
		-ms-animation: none !important;
		animation: none !important;
	}
}


	
	

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px){
	article{

		iframe{
				height: 315px;
			}
	}
	.pager-body li{
		margin-left: -5px;
	}
}
		

