body{
	font-family: $roboto;
	font-weight: normal;
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 21px;
	color: $text-color;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid{
	color: red;
}
body input:required:valid,
body textarea:required:valid{
	color: green;
}
input{
	outline: none;
}

ul, li{
	margin: 0;
	padding: 0;
}
.responsive-img{
	width: 100%;
	display: block;
}
.clearfix{
	@include clearfix;
}
.text{
  &-center{
    text-align: center;
  }
  &-right{
    text-align: right;
  }
}
.cover{
  position: relative;
  &:before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
    background-color: rgba(#000, 0.7);
  }
}
.justify{
	content: '';
	width: 100%;
	height: 0;
	display: inline-block;
	overflow: hidden;
	visibility: hidden;
	line-height: 0;
}
.hidden{
	display: none;
}
.banner{
	margin-top: 30px;
	img{
		width: 100%;
	}
}
.mobile-menu--hidden{
    display: none;
}
h3, h4{
    color: $accent;
    a{
        color: $accent;
        &:hover, &:active{
            color: $accent;
        }
        
    }
}
h2{
    position: relative;
    margin-top: 0;
    margin-bottom: 14px;
    font-size: 24px;
    span{
        position: relative;
        display: inline-block;
        margin-right: 50px;
        padding-right: 17px;
        background-color: #fff;
        z-index: 2;
    }

    &:after{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 1px;
        left: 0;
        top: 50%;
        margin-top: 3px;
        background-color: $accent;
        z-index: 1;
    }
}
h3{
    margin-top: 0;
    font-size: 18px;
    line-height: 24px;
}
.main-article{
    h3{
        position: relative;
        padding-bottom: 9px;
        &:after{
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 180px;
            height: 1px;
            display: block;
            background-color: $accent;
        }
    }
}
h4{
    font-size: 16px;
    margin-top: -4px;
    margin-bottom: 7px;
    // letter-spacing: -.85px;
}

.toggle-mnu{
position: absolute;
// float: right;
top: 0;
right: 15px;
  width: 32px;
  height: 28px;
  // margin-top: 5px;
  z-index: 100;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  } 

  span:after{

    top: 18px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 3px;
    background-color: $accent;
    transition: all 0.3s;
    backface-visibility: hidden;
    // border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
} 


