article{
	@include clearfix;
	line-height: 28px;
	img{
		max-width: 100%;
		float: left;
		padding: 10px;
		margin: 15px;
		margin-left: 0;
		border: 1px solid $accent;
	}
	p{
		margin-top: 15px;
	}
	a{
		color: $accent;
		text-decoration: underline;
		&:hover, &:active{
			color: $accent;
		}
		
	}

	figure{
		@include clearfix;
		padding: 10px 30px 0 15px;
		margin: 30px 0;
		border: 1px solid $accent;
		div{
			float: left;
			width: 273px;
			height: 240px;
			margin-bottom: 20px;
			// margin-right: 15px;
			overflow: hidden;

			img{
			max-width: none;
			float: none;	
			padding: 0;
			margin: 0;
			border: none;
			}
		}

		p{
			margin-left: 288px;
			margin-top: 0;
			margin-bottom: 20px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}

	ul{
		li{
			position: relative;
			margin-bottom: 20px;
			padding-left: 27px;
			list-style-type: none;
			&:before{
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 3px;
				@include sprite($attention);
			}

		}
	}

	blockquote{
		position: relative;
		margin: 40px 0;
		padding: 15px 30px 15px 140px;
		position: relative;
		border-left: 4px solid #54c9d7;

		background: #AAD8DE url(../img/icons/mark.png) 40px center no-repeat;
		
		a{
			color: $text-color;
			&:hover, &:active{
				color: $text-color;
			}
			
		}
	}

	iframe{
		margin: 30px 0;
		width: 100%;
		height: 413px;
  	max-height: 100%;
  	border: none;
	}
	ol{
		margin-bottom: 20px;
		padding: 0;
		list-style-type: none;
			counter-reset: point;
		li{
			position: relative;
			padding-left: 30px;
			margin-bottom: 23px;

			&:before{
				content: counter(point);
  			counter-increment: point 1;
  			position: absolute;
  			left: 0;
  			top: 3px;
  			display: block;
  			width: 23px;
  			height: 23px;
  			background-color: $accent;
  			color: #fff;
  			font-size: 14px;
  			font-family: $roboto-b;
  			line-height: 22px;
  			text-align: center;
  			border-radius: 50%;

			}

		}
	}
}