header{
	margin-bottom: 30px;
}
.header-top{
	position: relative;
	padding-top: 20px;
	padding-bottom: 10px;
	background-color: $accent;
	
	nav{
		float: left;
	}

}
	.header-search{
		float: right;
	}
.menu__item{
		display: inline-block;
		margin-left: 27px;
		list-style-type: none;
		&:first-child{
			margin-left: 0;
		}
	}
	.menu__link{
		color: #fff;
		&:hover, &:active{
			color: #fff;
		}
		

	}
.header-logo{
	@include absolute-center;
	top: 36px;
	width: 165px;
	height: 165px;
	box-shadow: 0 2px 1px rgba(0,0,0,.36);
	border-radius: 82px;
	background-color: $accent;
	text-align: center;
	img{
		@include absolute-center;
	}
	span{
		display: inline-block;
		margin-top: 120px;
		color: #fff;
	}
}
.header-search{
	position: relative;
	width: 248px;
	height: 34px;
}
.header-search__input{
	position: absolute;
	display: none;
	right: 0;
	top: 0px;
width: 248px;
	border-radius: 20px;
	background-color: #fff;

	input{
		padding: 5px 0;
		display: block;
		margin: 0 auto;
		width: 90%;
		border: none;
		@include placecolor($text-gray);
		font-style: italic;
	}
}
.header-search__button{
	display: inline-block;
	position: absolute;
	right: 0;
	top: 5px;
	@include sprite($search);
	&-active{
		position: absolute;
		right: 15px;
		top: 5px;
		border: none;
		background-color: transparent;
		@include sprite($search-active);
	}
}
.header-bottom{
	height: 109px;
	background:  url(../img/header-bg.jpg) center top no-repeat;
	background-size: cover;
}